@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.Homepage {
  background: linear-gradient(-45deg, #1a1d1e, #aeb6b8, #525152, #151616);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
  .top-bar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 15px 30px;
    background-color: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(8px);
  }

  .Homepage .top-bar .nav-left ul {
    display: flex;
    align-items: center;
    gap: 30px;
    list-style: none;
    margin: 0;
    padding-right: 20px;
  }

  @media screen and (max-width: 768px) {
    .top-bar {
      padding: 10px 15px;
    }

    .Homepage .top-bar .nav-left ul {
      gap: 15px;
      padding-right: 10px;
    }

    .Homepage .top-bar .nav-left a {
      font-size: 14px;
    }

    .login-btn {
      padding: 8px 15px;
      font-size: 14px;
    }
  }

  @media screen and (max-width: 480px) {
    .Homepage .top-bar .nav-left ul {
      gap: 10px;
    }

    .Homepage .top-bar .nav-left a {
      font-size: 12px;
    }
  }
.Homepage .top-bar .nav-left a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  transition: all 0.3s ease;
}

.Homepage .top-bar .nav-left a:hover {
  transform: scale(1.1);
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.nav-right {
  display: flex;
  margin-left: auto;
  padding-right: 75px;
}

.login-btn {
  color: white;
  background-color: rgba(19, 52, 8, 0.6);
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
  float: right;
}

.login-btn:hover {
  background-color: rgba(255, 255, 255, 0.3);
  transform: scale(1.05);
}
.welcome-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.welcome-container h1 {
  color: white;
  font-size: 3rem;
  text-align: center;
}

.choose-role-btn {
font-family: 'Helvetica Neue', Arial, sans-serif;
font-size: 1.10rem;
font-weight: 400;
color: #ffffff;
background-color: rgba(19, 52, 8, 0.2);
border: none;
border-radius: 25px;
padding: 15px 30px;
cursor: pointer;
transition: background-color 0.3s ease;
position: absolute;
top: 60%;
left: 50%;
transform: translateX(-50%);
}

.choose-role-btn:hover {
background-color: rgba(255, 255, 255, 0.3);
}
